<template>
  <div>
    <div class="search">
      <el-form inline :model="form" class="form">
        <el-form-item label="角色名称">
          <el-input style="width: 150px" v-model="form.name" clearable placeholder="请输入角色名称" />
        </el-form-item>
        <el-form-item label="角色状态">
          <el-select v-model="form.status">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
      </el-form>
      <el-button style="margin-bottom: 18px;">重置</el-button>
      <el-button style="margin-bottom: 18px;" type="primary">查询</el-button>
    </div>
    <div class="top">
      <el-button type="primary" @click="addHandle">新增</el-button>
    </div>
    <el-table
      :data="dataSource"
      style="width: 100%"
    >
      <el-table-column prop="name" label="角色名称" />
      <el-table-column prop="time" label="创建时间" />
      <el-table-column prop="desc" label="角色描述" />
      <el-table-column prop="status" label="角色状态" />
      <el-table-column prop="oprate" label="操作">
        <template #default="{row}">
          <div>
            <el-link style="margin-right: 10px;" type="primary" @click="editHandle(row)">编辑</el-link>
            <el-link type="primary" @click="deleteHandle(row)">删除</el-link>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <Modal
      :detail="detail"
      :visible="visible"
      @handleClose="handleClose"
    />
  </div>
</template>
<script>
import { reactive, ref } from 'vue'
import Modal from './modal'
import { ElMessage } from 'element-plus'
import { options } from './config'

export default {
  components: {
    Modal
  },
  setup() {
    const visible = ref(false)
    const detail = ref(null)
    const form = reactive({
      name: '',
      status: '1'
    })
    const dataSource = ref([
      { name: '长河', time: '2021-01-02', desc: '胜多负少', status: true }
    ])

    const addHandle = () => {
      detail.value = null
      visible.value = true
    }

    const editHandle = row => {
      visible.value = true
      detail.value = row
    }

    const deleteHandle = row => {
      ElMessage.success('删除成功')
    }
    const handleClose = () => {
      visible.value = false
    }

    return {
      form,
      detail,
      visible,
      options,
      dataSource,
      addHandle,
      editHandle,
      deleteHandle,
      handleClose
    }
  },
}
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>
