<template>
  <el-dialog
    title="添加角色"
    v-model="dialogVisible"
    width="30%"
    :before-close="handleClose"
  >
    <el-form ref="formRef" :model="form" :rules="rules" style="width: 100%;" label-position="top">
      <el-form-item label="角色名称" prop="name">
        <el-input v-model="form.name" clearable placeholder="请输入角色名称" />
      </el-form-item>
      <el-form-item style="margin-bottom: 30px;" label="角色描述" prop="desc">
        <el-input v-model="form.desc" clearable placeholder="请输入角色描述" />
      </el-form-item>
      <el-form-item prop="status">
        <el-switch
          v-model="form.status"
          inactive-text="角色状态"
        />
      </el-form-item>
      <el-form-item label="权限" prop="power">
        <el-tree
          ref="treeRef"
          :data="data"
          :props="{ children: 'children', label: 'label' }"
          show-checkbox
          node-key="id"
          default-expand-all
          @check="handleCheckChange"
        />
      </el-form-item>
    </el-form>
    <div class="footer">
      <el-button @click="handleClose">取消</el-button>
      <el-button type="primary" @click="addHandle">保存</el-button>
    </div>
  </el-dialog>
</template>
<script>
import { ref, reactive, watch } from 'vue'
import { ElMessage } from 'element-plus'
import { rules, data } from './config'
export default {
  props: {
    visible: Boolean,
    detail: Object
  },
  setup(props, { emit }) {
    const formRef = ref()
    const treeRef = ref()
    const dialogVisible = ref(false)
    const form = reactive({
      name: '',
      desc: '',
      status: true,
      power: null,
    })
    const handleClose = () => {
      emit('handleClose')
    }
    const handleCheckChange = (data, { checkedKeys }) => {
      form.power = checkedKeys
    }
    const addHandle = () => {
      formRef.value && formRef.value.validate(val => {
        if (val) {
          ElMessage.success('保存成功')
          formRef.value && formRef.value.resetFields()
          treeRef.value && treeRef.value.setCheckedKeys([])
          emit('handleClose')
        }
      })
    }
    watch(
      () => props.visible,
      val => {
        dialogVisible.value = val
        if (val) {
          const { name, desc, status, power } = props.detail ?? {}
          form.name = name
          form.desc = desc
          form.status = status
          form.power = power
        }
      }
    )
    return {
      form,
      formRef,
      treeRef,
      rules,
      data,
      dialogVisible,
      addHandle,
      handleClose,
      handleCheckChange
    }
  },
}
</script>
<style lang="scss" scoped>
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
